.project-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: $color1;

  h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }

  p {
    margin-bottom: 10px;
  }

  .project-content {
    border: 1px dashed $color1;
    padding: 30px;
    border-radius: 30px;

    @media only screen and (max-width: 700px) {
      padding: 15px;
      width: 450px;
    }

    @media only screen and (max-width: 450px) {
      padding: 15px;
      width: 200px;
      margin-bottom: 90px;
    }
  }

  .button {
    color: $black;
    font-size: 2rem;
    background: $color1;
    padding: 10px;
    border-radius: 20px;

    @media only screen and (max-width: 850px) {
      font-size: 1rem;
      padding: 5px;
    }
  }

  img {
    height: 450px;
    margin: 15px auto;

    @media only screen and (max-width: 450px) {
      height: 180px;
      width: 80px;
    }
  }
}
