.social-network {
  display: flex;
  justify-content: end;
  align-items: center;
  margin: 25px 30px;
  padding: 20px;

  .content {
    display: flex;
    gap: 15px;
    font-size: 2.5rem;

    @media only screen and (max-width: 630px) {
      font-size: 1.8rem;
      gap: 5px;
    }

    @media only screen and (max-width: 450px) {
      font-size: 1.1rem;
      gap: 5px;
    }

    li {
      color: $color1;
      margin-right: 27px;

      @media only screen and (max-width: 440px) {
        margin-right: 7px;
        margin-bottom: 10px;
      }
    }
  }
}
