.contact-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: $color1;
  font-size: 2rem;

  @media only screen and (max-width: 450px) {
    font-size: 1rem;
    height: 250px;
    margin-top: 150px;
    height: 50vh;
  }

  h2 {
    font-size: 4rem;
    letter-spacing: 3px;
    margin-top: -120px;
    margin-bottom: 50px;

    @media only screen and (max-width: 430px) {
      font-size: 1rem;
      margin-top: -50px;
      margin-bottom: 15px;
    }
  }

  .form-message {
    text-align: center;
  }

  .form-content {
    @media only screen and (max-width: 430px) {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    gap: 65px;
    margin-top: 30px;

    @media only screen and (max-width: 430px) {
      gap: 15px;
      margin-top: 10px;
    }

    input:not(:last-child) {
      width: 500px;
      height: 30px;
      font-size: 1.4rem;
      color: $color1;
      padding-left: 15px;
      border: transparent;
      border-bottom: 1px dashed black;
      transition: 0.3s ease-in-out;
      outline: none;

      @media only screen and (max-width: 430px) {
        height: 60px;
        width: 300px;
        font-size: 0.8rem;
        padding: 5px;
      }

      &:focus {
        border-bottom: 1px solid black;
      }
    }

    textarea {
      width: 100%;
      height: 180px;
      outline: none;
      border: 1px dashed black;
      border-radius: 10px;
      font-size: 1.1rem;
      padding: 10px;
      color: $color1;
      resize: none;

      @media only screen and (max-width: 430px) {
        height: 100px;
        width: 300px;
        font-size: 0.8rem;
      }
      &:focus {
        border: 1px solid black;
      }
    }

    .submit {
      cursor: pointer;
      width: 30%;
      outline: none;
      border: transparent;
      padding: 10px;
      text-align: center;
      background: $color1;
      color: black;
      border-radius: 20px;
      font-size: 2rem;

      @media only screen and (max-width: 430px) {
        font-size: 1rem;
        padding: 5px;
      }
    }
  }
}
