.navigation {
  position: absolute;
  top: 1rem;
  left: 1.4rem;
  font-size: 1.4rem;
  line-height: 28px;
  color: $color1;

  @media only screen and (max-width: 450px) {
    font-size: 1rem;
    top: 0.25rem;
  }

  &:before {
    content: "";
    position: absolute;
    height: 500px;
    width: 500px;
    background: rgba(0, 0, 0, 0.185);
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    top: -250px;
    left: -300px;
    z-index: -1;
    animation: navAnim 20s infinite alternate;

    @keyframes navAnim {
      50% {
        transform: scale(1.2);
      }
      100% {
        transform: scale(1);
      }
    }
    @media screen and (max-width: 430px) {
      height: 50px;
      width: 50px;
    }
  }

  ul li {
    color: $color1;
  }
  .nav-active {
    color: $black;
  }
  ul:hover li:not(:hover) {
    opacity: 0.5;
  }
  ul:hover li:hover {
    color: $black;
  }

  .nav-portfolio {
    cursor: pointer;
    display: flex;
    // padding-right: 6rem;
    transition: 0.5s;
    color: black;

    &:hover .nav-projects,
    .nav-projects:hover {
      visibility: visible;
      opacity: 1;

      li {
        transform: rotatey(0deg);
      }
    }

    .nav-projects {
      position: absolute;
      visibility: hidden;
      opacity: 0;
      padding: 0 40px;
      width: 300px;
      transform: translateX(70px);
      transition: 0.3s;
      perspective: 500px;

      @media only screen and (max-width: 450px) {
        font-size: 0.8rem;
        display: flex;
        justify-content: flex-start;
        gap: 5px;
      }

      li {
        transform: rotatey(30deg) scaleX(1.2);
        transform-origin: center;
        transition: 0.3s;
      }
    }
  }
}
