.top-container {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 20px;

  h2 {
    margin-bottom: 30px;
    letter-spacing: 1px;
    color: $color1;

    @media only screen and (max-width: 650px) {
      margin-bottom: 20px;
      letter-spacing: 1px;
      color: $color1;
    }

    @media only screen and (max-width: 450px) {
      letter-spacing: 1px;

      font-size: 1.2rem;
    }
  }
  p {
    letter-spacing: 1px;
    line-height: 70px;

    @media only screen and (max-width: 650px) {
      letter-spacing: 1px;
      line-height: 50px;
    }
    @media only screen and (max-width: 550px) {
      letter-spacing: 1px;
      line-height: 45px;
    }
    @media only screen and (max-width: 450px) {
      letter-spacing: 1px;
      line-height: 25px;
      font-size: 0.8rem;
    }
  }
}
