main {
  position: relative;
  height: 100vh;

  .ext-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 80vh;
    border: 1px solid transparent;

    h1 {
      border-bottom: 1px dashed $color1;
      font-size: 6.9rem;
      color: $black;
      letter-spacing: 5px;
      font-weight: 800;
      padding: 20px;
      text-shadow: 4px 5px 15px rgba(99, 98, 98, 0.87);
      position: relative;

      @media only screen and (max-width: 850px) {
        font-size: 2.7rem;
      }

      @media only screen and (max-width: 630px) {
        font-size: 2.8rem;
      }

      @media only screen and (max-width: 495px) {
        font-size: 2rem;
      }
      @media only screen and (max-width: 430px) {
        font-size: 1.6rem;
        border-bottom: transparent;
      }

      #C {
        font-size: 11.5rem;
        color: $color1;

        @media only screen and (max-width: 430px) {
          font-size: 5.6rem;
          border-bottom: transparent;
        }
      }
    }
    h1::after {
      content: "";
      clip-path: polygon(20% 0%, 67% 25%, 100% 100%, 35% 61%);
      width: 690px;
      height: 500px;
      left: -250px;
      top: -100px;
      background: rgba(0, 0, 0, 0.185);
      animation: navAnim 20s infinite alternate;
      position: absolute;
    }

    @keyframes navAnim {
      50% {
        transform: scale(1.2);
      }
      100% {
        transform: scale(1);
      }
    }

    h2 {
      margin-top: 35px;
      color: $color1;
      font-size: 2.5rem;

      @media only screen and (max-width: 850px) {
        font-size: 1.5rem;
      }

      @media only screen and (max-width: 630px) {
        font-size: 1.8rem;
      }

      @media only screen and (max-width: 495px) {
        font-size: 1.5rem;
      }

      @media only screen and (max-width: 430px) {
        font-size: 1.4rem;
      }
    }
  }
}
